<template>
  <div class="hello">
    <h1 style="font-size:50px;margin-bottom:20px">Visualizations</h1>
    <section class="info-tiles" style="margin-top:50px">
      <article class="tile is-child box">
        <b-button
          class="is-centered"
          type="is-primary"
          style="float:right"
          @click="addVisualization"
        >Create Visualization</b-button>

        <b-table :data="visualizations">
          <template slot-scope="props">
            <b-table-column label="Name" field="name" sortable searchable>{{props.row.name}}</b-table-column>
            <b-table-column label="Class" field="class" sortable searchable>{{props.row.class}}</b-table-column>
            <b-table-column label="Field" field="field" sortable searchable>{{props.row.field}}</b-table-column>
            <b-table-column
              label="Field Name"
              field="fieldName"
              sortable
              searchable
            >{{props.row.fieldName}}</b-table-column>
            <b-table-column
              label="Authorized"
              field="authorized"
              sortable
              searchable
            >{{props.row.authorized}}</b-table-column>
            <b-table-column
              label="Group"
              field="group"
              sortable
              searchable
            >{{groups[props.row.group]}}</b-table-column>
            <b-table-column field="actions">
              <b-dropdown
                aria-role="list"
                class="is-pulled-right"
                position="is-bottom-left"
                style="cursor:pointer"
              >
                <b-icon icon="dots-vertical" slot="trigger"></b-icon>
                <b-dropdown-item
                  aria-role="listitem"
                  @click="dataToEdit = props.row;visualizationModalActive = true;"
                >
                  <b-icon icon="pencil"></b-icon>
                  <span
                    style="margin-left:15px;font-size:15px"
                  >Edit</span>
                </b-dropdown-item>
                <b-dropdown-item aria-role="listitem" @click="deleteView(props.row.uuid)">
                  <b-icon icon="delete"></b-icon>
                  <span
                    style="margin-left:15px;font-size:15px "
                  >Delete</span>
                </b-dropdown-item>
              </b-dropdown>
            </b-table-column>
          </template>
          <template slot="empty">
            <section class="section">
              <div class="content has-text-grey has-text-centered">
                <p>
                  <b-icon icon="emoticon-sad" size="is-large"></b-icon>
                </p>
                <p>Nothing here.</p>
              </div>
            </section>
          </template>
        </b-table>
      </article>
    </section>
    <b-modal :active.sync="visualizationModalActive" has-modal-card trap-focus>
      <AddEditVisualization></AddEditVisualization>
    </b-modal>
  </div>
</template>

<script>
import AddEditVisualization from "./AddEditVisualization";

export default {
  data() {
    return {
      visualizations: [],
      visualizationModalActive: false
    }
  },
  mounted() {
    this.fetchData();
  },
  methods: {
    addVisualization () {
      this.visualizationModalActive = true
    },
    fetchData() {
      fetch('/api/view/list')
        .then(response => {
          return response.json
        })
        .then(json => {
          this.visualizations = json.views
        })
    }
  },
  components: {
    AddEditVisualization
  }
};
</script>

