<template>
  <div class="modal-card" style="width:800px">
    <section class="info-tiles" style="margin-top:50px">
      <article class="tile is-child box">
        <h1 style="font-size:50px;margin-bottom:20px; text-align:center">Visualization</h1>
        <b-steps
        style="height:600px;width:100%;color:'red'"
        v-model="activeStep"
        :has-navigation="false">
          <b-step-item style="height:400px" label="Type">
            <div class="has-text-centered">
              <h1 class="title has-text-centered">Select Display Type</h1>
              <p>{{ view.class == undefined ? "none" : view.class }} selected</p>
              <b-button
                class="type-button"
                size="is-large"
                icon-left="chart-line"
                @click="setViewClass('line')"
                :type="view.class == 'line' ? 'is-primary' : 'none'"
              >Line</b-button>
              <b-button
                class="type-button"
                size="is-large"
                icon-left="chart-bar"
                style="margin-left:50px"
                @click="setViewClass('bar')"
                :type="view.class == 'bar' ? 'is-primary' : 'none'"
              >Bar</b-button>
              <br />
              <b-button
                class="type-button"
                size="is-large"
                icon-left="chart-pie"
                @click="setViewClass('pie')"
                :type="view.class == 'pie' ? 'is-primary' : 'none'"
              >Pie</b-button>
              <b-button
                class="type-button"
                size="is-large"
                icon-left="table"
                style="margin-left:50px"
                @click="setViewClass('table')"
                :type="view.class == 'table' ? 'is-primary' : 'none'"
              >Table</b-button>
            </div>
          </b-step-item>

          <b-step-item style="height:400px" label="Information">
              <h1 class="title has-text-centered">Information</h1>
              <LineInformation @changedView="changedView"></LineInformation>
              <PieInformation v-if="view.class == 'pie'" @changedView="changedView"></PieInformation>
              <BarInformation v-if="view.class == 'bar'" @changedView="changedView"></BarInformation>
              <TableInformation v-if="view.class == 'table'" @changedView="changedView"></TableInformation>
          </b-step-item>
          <b-step-item style="height:400px" label="Finish" disabled>
            <h1 class="title has-text-centered">Finish</h1>
            <div class="columns is-mobile is-centered">
              <div class="column is-half">
                <b-field label="Visualization Name">
                  <b-input v-model="view.name" placeholder="Enter a name..." required></b-input>
                </b-field>
                <b-button
                  @click="saveView"
                  type="is-primary"
                  style="float:right;width: 100%"
                ><b-icon icon="check"></b-icon><span>Submit</span></b-button>
              </div>
            </div>
            </b-step-item>
            <div class="columns">
              <div class="column">
                <b-button
                  outlined
                  type="is-danger"
                  icon-pack="fas"
                  icon-left="backward"
                  @click="stepBackward"
                  style="float:left"
                  v-if="activeStep > 0">
                  Previous
              </b-button>
              <b-button
                  outlined
                  type="is-success"
                  icon-pack="fas"
                  icon-right="forward"
                  @click="stepForward"
                  style="float: right"
                  v-if="activeStep < 2">
                  Next
              </b-button>
            </div>
          </div>
        </b-steps>
      </article>
    </section>
  </div>
</template>

<script>
import LineInformation from './views/LineInformation'
import PieInformation from './views/PieInformation'
import BarInformation from './views/BarInformation'
import TableInformation from './views/TableInformation'
export default {
  name: "AddVisualization",
  data() {
    return {
      view: {
        group: undefined,
        authorized: undefined,
        name: "",
        class: "",
        fields: [],
        fieldNames: []
      },
      activeStep: 0,
      allGroups: [],
      authorizedAssets: [],
      allFields: []
    };
  },
  mounted() {
  },
  methods: {
    changedView(view) {
      this.view = view
    },
    setViewClass(type) {
      this.view.class = type
    },
    saveView() {
      fetch('/api/view/add', {
        method: 'post',
        body: JSON.stringify(this.view)
      })
        .then(response => response)
        .then(data => {
          if (data.status === 200) {
            alert("success")
          }
        })
    },
    stepForward() {
      if (this.activeStep < 2) {
        this.activeStep++
      }
    },
    stepBackward() {
      if (this.activeStep !== 0) {
        this.activeStep--
      }
    }
  }
};
</script>

<style scoped lang="scss">
.type-button {
  width: 150px;
}
.button {
  margin-top: 20px;
  margin-bottom: 20px;
}
</style>
