<template>
  <div class="hello">
    <h1 style="font-size:50px;margin-bottom:20px">Groups</h1>
    <section class="info-tiles" style="margin-top:50px">
      <article class="tile is-child box">
        <b-button
          class="is-centered"
          type="is-primary"
          style="background-color:#712844; float:right"
          @click="openAddModal"
          :disabled="role == Roles.SuperUser"
        >Create Group</b-button>
        <b-table :data="groups" ref="table">
          <template slot-scope="props">
            <b-table-column label="Name" field="name" sortable searchable>{{props.row.name}}</b-table-column>
            <b-table-column label="Uuid" field="uuid" sortable searchable>{{props.row.uuid}}</b-table-column>
            <b-table-column field="actions">
              <b-dropdown
                aria-role="list"
                class="is-pulled-right"
                position="is-bottom-left"
                style="cursor:pointer"
              >
                <b-icon icon="dots-vertical" slot="trigger"></b-icon>
                <b-dropdown-item
                  aria-role="listitem"
                  @click="dataToEdit = props.row;editUserData = true;"
                >
                  <b-icon icon="pencil"></b-icon>
                  <span
                    style="margin-left:15px;font-size:15px"
                    :disabled="role != Roles.SuperUser"
                  >Edit</span>
                </b-dropdown-item>
                <b-dropdown-item aria-role="listitem" @click="deleteUser(props.row.uuid)">
                  <b-icon icon="delete"></b-icon>
                  <span
                    style="margin-left:15px;font-size:15px "
                    :disabled="role != Roles.SuperUser"
                  >Delete</span>
                </b-dropdown-item>
              </b-dropdown>
            </b-table-column>
          </template>
          <template slot="empty">
            <section class="section">
              <div class="content has-text-grey has-text-centered">
                <p>
                  <b-icon icon="emoticon-sad" size="is-large"></b-icon>
                </p>
                <p>Nothing here.</p>
              </div>
            </section>
          </template>
        </b-table>
      </article>
    </section>
    <b-modal :active.sync="editUserData" has-modal-card>
      <EditGroup :passedObject="dataToEdit"></EditGroup>
    </b-modal>
    <b-modal :active.sync="addUserModal" has-modal-card>
      <AddGroup></AddGroup>
    </b-modal>
  </div>
</template>

<script>
import { Role } from "@/_helpers/role.js";
import EditGroup from "./EditGroup";
import AddGroup from "./AddGroup";

export default {
  name: "AddVisualization",
  props: {
    type: String
  },
  data() {
    return {
      groups: [
        {
          uuid: "d90201b0-07f9-4752-a7ef-b536681f43d6",
          name: "McMaster University"
        }
      ],
      editUserData: false,
      addUserModal: false,
      dataToEdit: undefined,
      role: undefined,
      uuid: undefined,
      Roles: Role
    };
  },
  mounted() {
    this.fetchData();
  },
  methods: {
    fetchData() {
      this.role = JSON.parse(localStorage.getItem("User")).class;
      this.role = JSON.parse(localStorage.getItem("User")).uuid;
      fetch(process.env.VUE_APP_ENDPOINT + "group/list")
        .then(response => {
          if (response.ok) {
            return Promise.all([response.ok, response.json()]);
          } else {
            return Promise.all([response.ok, response.text()]);
          }
        })
        .then(response => {
          this.groups = [];
          const status = response[0];
          const data = response[1];
          if (!status) {
            this.$buefy.snackbar.open(data);
          }
          this.groups.push({
            uuid: data.current.uuid,
            name: data.current.name
          });
          for (let i = 0; i < data.others.length; i++) {
            this.groups.push({
              uuid: data.others[i].uuid,
              name: data.others[i].name
            });
          }
        });
    },
    openAddModal() {
      const modal = this.$buefy.modal.open({
        parent: this,
        component: AddGroup,
        hasModalCard: true
      });
      modal.$on("close", () => {
        this.fetchData();
      });
    },
    changeType(type) {
      this.graphType = type;
    },
    deleteUser(uuid) {
      const group = {
        uuid: uuid
      };
      fetch(process.env.VUE_APP_ENDPOINT + "group/delete", {
        method: "post",
        body: JSON.stringify(group)
      })
        .then(response => {
          if (response.ok) {
            return Promise.all([response.ok, response.json()]);
          } else {
            return Promise.all([response.ok, response.text()]);
          }
        })
        .then(response => {
          const status = response[0];
          const data = response[1];
          if (!status) {
            this.$buefy.snackbar.open(data);
          }
          this.$buefy.snackbar.open(data);
          setTimeout(this.fetchData(), 5000);
        });
    }
  },
  components: { EditGroup, AddGroup }
};
</script>

