<template>
  <form action>
    <div class="modal-card" style="width: 500px">
      <header class="modal-card-head">
        <p class="modal-card-title">Edit</p>
      </header>
      <section class="modal-card-body">
        <b-field label="Name">
          <b-input type="text" v-model="passedObject.name" placeholder="Name..." required></b-input>
        </b-field>
        <b-field label="Uuid">
          <b-input disabled type="text" v-model="passedObject.uuid" placeholder="Uuid..." required></b-input>
        </b-field>
      </section>
      <footer class="modal-card-foot" style="display: flex;justify-content: flex-end">
        <b-button class="button" type="button" @click="$parent.close()">Close</b-button>
        <b-button type="is-primary" style="background-color:#712844" @click="save">Save</b-button>
      </footer>
    </div>
  </form>
</template>

<script>
import { Role } from '@/_helpers/role.js';

export default {
  props: {
    passedObject: {}
  },
  data() {
    return {
      uuid: this.passedObject.uuid,
      loggedIn: {
        role: undefined,
        uuid: undefined
      }
    };
  },
  mounted() {
    this.loggedIn.role = JSON.parse(localStorage.getItem('User')).class;
    this.loggedIn.uuid = JSON.parse(localStorage.getItem('User')).uuid;
  },
  methods: {
    save() {
      fetch(
        process.env.VUE_APP_ENDPOINT + "group/update",
        {
          method: "post",
          body: JSON.stringify(this.passedObject)
        }
      )
        .then(response => {
          if (response.ok) {
            return Promise.all([response.ok, response.json()]);
          } else {
            return Promise.all([response.ok, response.text()]);
          }
        })
        .then(response => {
          const status = response[0];
          const data = response[1];
          if (!status) {
            this.$buefy.snackbar.open(data);
            return
          }
          this.$buefy.snackbar.open(data);
          this.$parent.close();
        });
    }
  },
  components: {}
};
</script>
