<template>
  <div class="columns is-mobile is-centered">
    <div class="column is-half">
      <b-field label="Group">
        <b-select expanded v-model="group" placeholder="Group...">
          <option
            v-for="group in allGroups"
            :value="group.uuid"
            :key="group.uuid"
          >{{ group.name }}</option>
        </b-select>
      </b-field>
      <div class="columns">
        <div class="column">
          <b-field label="X-Axis">
            <b-select v-model="fields[0]"
              placeholder="Select field..."
              expanded>
              <option v-for="field in allFields" :value="field.name" :key="field"> {{ field.name }} </option>
            </b-select>
          </b-field>
          <b-field label="Y-Axis">
            <b-select v-model="fields[1]"
              placeholder="Select field..."
              expanded>
              <option v-for="field in allFields" :value="field.name" :key="field"> {{ field.name }} </option>
            </b-select>
          </b-field>
            </div>
            <div class="column">
          <b-field label="Name">
            <b-input v-model="fieldNames[0]" placeholder="Enter a name..."></b-input>
          </b-field>
          <b-field label="Name">
            <b-input v-model="fieldNames[1]" placeholder="Enter a name..."></b-input>
          </b-field>
        </div>
      </div>
      <b-field label="Authorized">
        <b-select
          expanded
          v-model="authorized"
          placeholder="Authorized Asset..."
        >
          <option v-for="auth in authorizedAssets" :value="auth" :key="auth">{{ auth }}</option>
        </b-select>
      </b-field>
    </div>
  </div>
</template>

<script>
export default {
  data () {
    return {
      allGroups: [],
      allFields: [],
      allAuthorized: [],
      view: {
        class: "line",
        group: "",
        fields: [],
        fieldNames: [],
        authorized: ""
      }

    }
  },
  mounted () {
    this.fetchFields()
    this.fetchGroups()
    this.fetchAuthorized()
  },
  watch: {
    view: function () {
      this.$emit('changedView', this.view)
    }
  },
  methods: {
    fetchFields() {
      fetch('/api/fields/list')
        .then(response => {
          return response.json()
        })
        .then(json => {
          this.allFields = json.fields
        })
    },
    fetchGroups() {
      console.log("fetching groups")
      fetch('/api/group/list')
        .then(response => {
          return response.json()
        })
        .then(json => {
          this.allGroups.push(json.current)
          if (json.others !== null) {
            json.others.forEach(group => {
              this.allGroups.push(group)
            })
          }
        })
    },
    fetchAuthorizedAssets() {
      fetch('/api/assets/list')
        .then(response => {
          return response.json()
        })
        .then(json => {
          this.authorizedAssets = json.assets
        })
    }
  }
}
</script>
